<template>
  <div class="about">
    <h1>Login</h1>
    <form @submit.prevent="login">
      <input v-model="email" type="text" placeholder="email" />
      <input v-model="password" type="password" placeholder="contraseña" />
      <button type="submit" value="acceder">Submit</button>
    </form>
    <p>{{ mensaje }}</p>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  data() {
    return {
      email: "",
      password: "",
      mensaje: ""
    };
  },
  methods: {
    login() {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then((user) => this.$router.replace('account'), (error) => this.mensaje = error)
        
    },
  },
};
</script>
